@import "component_styles";

.WBBBandwidthReportComponent {
  padding-bottom: 20px;
  .WBBSpeedTestResults {
    text-align: center;
    @media (min-width: $width-boostrap-md) {
      text-align: right;
      h4 {
        margin-top: 10px;
        font-size: 24px;
        white-space: nowrap;
      }
    }
  }
  .WBBSpeedMeasurementPlaceholder {
    text-align: center;
    @media (min-width: $width-boostrap-md) {
      text-align: left;
      .WBBValue {
        font-size: 20px;
      }
    }
    .WBBDownload {
      text-align: right;
    }
    .WBBUpload {
      text-align: left;
    }
  }
}