@import "component_styles";

.WBBLatencyStageReport {
  margin: 0 5px 0 5px;
  @media (min-width: $width-boostrap-sm) {
    margin: 0 20px 0 20px;
  }
  h5 {
    font-size: 14px;
  }
  .WBBBoxPlotContainer {
    padding-left: 0;
    @media (min-width: $width-boostrap-md) {
      padding-left: $bootstrap-col-padding;
      margin-left: -60px;
    }
    @media (min-width: $width-boostrap-lg) {
      margin-left: 0;
    }
  }
  .WBBStageLatencyStats {
    display: none;
    @media (min-width: $width-boostrap-md) {
      display: block;
      width: 33%;
    }
    @media (min-width: $width-boostrap-lg) {
      width: 25%;
    }
  }
  > .row {
    margin: 0;
    > div:nth-last-child(3) {
      white-space: nowrap;
    }
    .title {
      @media (min-width: $width-boostrap-sm) {
        padding-left: 0px;
      }
      h5 {
        font-size: 12px;
        white-space: nowrap;
        @media (min-width: $width-boostrap-sm) {
          font-size: 15px;
        }
        @media (min-width: $width-boostrap-lg) {
          font-size: 17px;
        }
      }
      padding-right: 0;
    }
    .col-sm-10 {
      padding-left: 0;
    }
  }
  .spacer {
    height: 5px;
    background-color: white;
    margin-bottom: 10px;
  }
}

