@import "component_styles";

$button-color-dark: #5D5D5D;
$border-color-dark: #979797;

.WBBFooter.WBBResultsLink {
  background: #DFDFDF;
  padding: 20px 10px 20px 10px;
  @media (min-width: $width-boostrap-md) {
    padding: 20px 20px 20px 20px;
  }

  .form-group {
    display: block;
    margin-bottom: 0;
  }
  .row {
    margin-left: 0;
  }

  label[for="shareResults"] {
    span {
      display: none;
      @media (min-width: $width-boostrap-sm) {
        display: inline;
      }
    }
  }

  #shareResults {
    margin-left: 10px;
    margin-right: 10px;
    border: 1px solid $border-color-dark;
    color: #878787;
    display: inline;

    width: calc(100% - 143px);

    @media (min-width: $width-boostrap-sm) {
      width: 40%;
    }
  }
  .copy-button {
    background: $button-color-dark;
    border-radius: 5px;
    font-weight: bold;
    font-size: 14px;
    color: #FFFFFF;
    float: right;
    @media (min-width: $width-boostrap-sm) {
      float: none;
    }
    img {
      margin-left: 5px;
    }
  }
  .test-again {
    button {
      font-weight: bold;
      padding: 10px 20px 10px 20px;
      font-size: 16px;
      @media (min-width: $width-boostrap-sm) {
        padding: 7px 15px 7px 15px;
        font-size: 14px;
      }
    }
    text-align: center;
    margin-top: 10px;
    @media (min-width: $width-boostrap-sm) {
      margin-top: 0;
      float: right;
    }
  }
}
