// common variables
$primary-medium: #005888;
$primary-dark: #024e79;
$background-gray: #efefef;
$main-test-card-white: #f9f9f9;
$error-red: #980E1F;
$success-green: #447400;

$width-iphone-se: 320px;
$width-wbb-xs: 400px;
$width-boostrap-sm: 576px;
$width-boostrap-md: 768px;
$width-boostrap-lg: 992px;
$width-boostrap-xl: 1200px;
$bootstrap-col-padding: 15px;


.WBBNoPadding {
  padding-left: 0;
  padding-right: 0;
}


// button styles
button.btn-primary {
  background-color: $primary-medium;
  border-color: $primary-medium;
}

.WBBCard {
  border: none;
  .card-header {
    background-color: $primary-medium;
    color: white;
  }
  .card-header:first-child {
  }
  h2 {
    margin-top: 0;
    margin-bottom: 0;
  }
  .card-body, .WBBFooter {
    background-color: $background-gray;
  }
}

.container.WBBContainer {
  padding-left: 0;
  padding-right: 0;
  .row {
    margin-right: 0;
  }

}

$main-card-border-radius: 20px;

.WBBMainTestCard {
  margin-top: 0;
  margin-bottom: 50px;

  .WBBStartRunningTheTest {
    padding: 10px 10px 0 10px;
    button {
      margin-top: 10px;
    }
    @media (min-width: $width-boostrap-sm) {
      padding: 20px 10px 0 10px;
    }
  }

  .WBBFooter {
    min-height: 30px;
  }

  @media (min-width: $width-boostrap-md)  {
    margin-top: 35px;
    .card-header {
      border-top-left-radius: $main-card-border-radius;
      border-top-right-radius: $main-card-border-radius;
    }
    > .WBBFooter {
      border-bottom-left-radius: $main-card-border-radius;
      border-bottom-right-radius: $main-card-border-radius;
      //background-color: red;
    }
  }

  @media (min-width: $width-boostrap-lg)  {
    margin-top: 35px;
    > .card-body {
      padding: 20px 30px 30px 30px;
    }
  }

  > .card-body {
    padding-top: 10px;
  }

  .WBBLatencySummary {
    padding: 10px 5px 20px 5px;
    margin: 0 5px 10px 5px;
    @media (min-width: $width-boostrap-md)  {
      padding: 20px 0px 20px 0px;
    }
    @media (min-width: $width-boostrap-lg)  {
      padding: 20px 30px 20px 20px;
    }
    background-color: $main-test-card-white;
    margin-bottom: 20px;
    > .row {
      margin-left: 0;
      h3 {
        margin-top: 0;
        margin-bottom: 20px;
        font-size: 15px;
        @media (min-width: $width-boostrap-sm)  {
          margin-top: 0;
          font-size: 24px;
          margin-bottom: 30px;
        }
      }
      text-align: center;
      p {
        display: none;
      }
      @media (min-width: $width-boostrap-md) {
        h3 {
          margin-bottom: 20px;
        }
        text-align: left;
        p {
          display: block;
        }
      }
    }
    .WBBMeasurementsCalloutsContainer {
      padding: 0;
      .callout {
        padding: 0 0 0 0;
        @media (min-width: $width-boostrap-lg)  {
          padding: 0 $bootstrap-col-padding 0 $bootstrap-col-padding;
        }
        .condition {
          font-size: 13px;
          @media (min-width: $width-boostrap-sm)  {
            font-size: 14px;
          }
          @media (min-width: $width-boostrap-lg)  {
            font-size: 16px;
          }
        }
        .latency {
          white-space: nowrap;
          &.good {
            color: $success-green;
          }
          &.bad {
            color: $error-red;
          }
          margin-top: 10px;
          font-size: 16px;
          @media (min-width: $width-boostrap-sm)  {
            font-size: 18px;
          }
          @media (min-width: $width-boostrap-md)  {
            font-size: 16px;
          }
          @media (min-width: $width-boostrap-lg)  {
            font-size: 20px;
          }
        }
      }
    }
    p {
      margin-bottom: 0;
    }
  }
}
