@import "component_styles";


.WBBCardTitle {
  text-align: center;
  padding: 25px $bootstrap-col-padding 20px $bootstrap-col-padding;
  h2 {
    font-size: 22px;
    @media (min-width: $width-boostrap-sm) {
      font-size: 30px;
    }
  }
}

.WBBCardTitle.WBBTestingDone {
  height: auto;
  text-align: left;
  padding: 25px $bootstrap-col-padding 10px $bootstrap-col-padding;


  @media (min-width: $width-boostrap-sm) {
    padding: 25px 25px 20px 25px;
  }

  h2 {
    margin-top: 0px;
    margin-left: 0px;
    font-size: 22px;
    @media (min-width: $width-boostrap-sm) {
      padding: 10px 0px 0px 0px;
      font-size: 30px;
    }
  }

  .indicator {
    width: 50%;
    font-size: 11px;
    display: inline-block;
    img {
      margin-right: 5px;
    }
    margin-bottom: 10px;
    @media (min-width: $width-wbb-xs) {
      font-size: 14px;
    }
    @media (min-width: 610px) {
      width: 33%;
      margin-bottom: 5px;
    }
    @media (min-width: 992px) {
      width: auto;
      &:first-child {
        margin-left: 0px;
      }
      margin-left: 40px;
    }
  }
  .WBBCategoryContainer {
    margin: 10px 0 0 0;
  }

  .WBBHowToFixSmallScreen {
    color: white;
    margin-top: 15px;
    font-weight: bold;
    text-decoration: underline;
    display: block;
    @media (min-width: $width-boostrap-md) {
      display: none;
    }
  }

  .WBBYourGrade {
    margin: 0;
    h2 {
      float: left;
      margin-bottom: 10px;
    }
    p {
      display: none;
      @media (min-width: $width-boostrap-md) {
        display: block;
      }
      font-weight: bold;
      color: white;
      margin-top: 15px;
      float: right;
      a {
        color: white;
        text-decoration: underline;
      }
    }
  }
}
